import Vue from 'vue';
import Vuex from 'vuex';
import { DaptinClient } from 'daptin-client';
Vue.use(Vuex);
var token = window.localStorage.getItem("token");
var daptinClient = new DaptinClient("http://dashboard.callup.ai:8080", false);
daptinClient.worldManager.loadModels().then(function () {
    if (token) {
        daptinClient.jsonApi.findAll("ticket", {
            included_relations: "intent",
            page: state.ticketPagination
        }).then(function (res) {
            console.log("all tickets", res.data);
            state.tickets = res.data;
        });
        daptinClient.jsonApi.findAll("intent").then(function (res) {
            console.log("all intents", res.data);
            state.intents = res.data;
        });
    }
});
var mutations = {
    reverse: function (state) { return state.links.reverse(); },
    logout: function (state) {
        state.user = {};
    },
    refreshTickets: function (state) {
        if (state.user.token) {
            daptinClient.jsonApi.findAll("ticket", {
                included_relations: "intent",
                page: state.ticketPagination
            }).then(function (res) {
                console.log("all tickets", res.data);
                state.tickets = res.data;
            });
        }
    },
    refreshIntents: function (state) {
        if (state.user.token) {
            daptinClient.worldManager.loadModel("intent").then(function (response) {
                daptinClient.jsonApi.findAll("intent").then(function (tickets) {
                    console.log("intents", tickets);
                    state.intents = tickets;
                });
            });
        }
    }
};
var actions = {};
var state = {
    links: [
        { url: "https://vuejs.org", description: "Core Docs" },
    ],
    tickets: [],
    intents: [],
    client: daptinClient,
    user: {
        token: token
    },
    ticketPagination: {
        size: 100,
        number: 1
    }
};
export default new Vuex.Store({
    state: state,
    mutations: mutations,
    actions: actions
});



